import { NgModule } from "@angular/core";
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { ShowResourceComponent } from './show-resource/show-resource.component';

export const resourceRoutes = [
    { path:'show-resource', component : ShowResourceComponent }
]

@NgModule({
    imports : [ CommonModule, RouterModule.forChild(resourceRoutes)],
    declarations : [ShowResourceComponent]
})
export class ResourceModule{}